
@import url(../static/styles/sn-font/iconfont.css);
.sn-btn{display:inline-block;line-height:1;white-space:nowrap;-webkit-appearance:none;text-align:center;box-sizing:border-box;transition:.1s;font-weight:500;-moz-user-select:none;-webkit-user-select:none;-ms-user-select:none;color:#fff;border-radius:4px;outline:none;border:1px solid #9966FF;padding:12px 20px;font-size:14px;cursor:pointer
}
.sn-btn--primary{background-color:#9966FF;border-color:#9966FF
}
.sn-btn--primary.sn-btn--primary:hover{background-color:#b9f;border-color:#b9f
}
.sn-btn--success{background-color:#13ce66;border-color:#13ce66
}
.sn-btn--success.sn-btn--success:hover{background-color:#29eb7f;border-color:#29eb7f
}
.sn-btn--warning{background-color:#f7ba2a;border-color:#f7ba2a
}
.sn-btn--warning.sn-btn--warning:hover{background-color:#f9ca5b;border-color:#f9ca5b
}
.sn-btn--danger{background-color:#ff4949;border-color:#ff4949
}
.sn-btn--danger.sn-btn--danger:hover{background-color:#ff7c7c;border-color:#ff7c7c
}
.sn-btn--info{background-color:#50bfff;border-color:#50bfff
}
.sn-btn--info.sn-btn--info:hover{background-color:#83d2ff;border-color:#83d2ff
}
.demo{margin:30px auto 0;width:500px
}


